import "core-js/modules/es.array.push.js";
import "core-js/modules/web.url-search-params.delete.js";
import "core-js/modules/web.url-search-params.has.js";
import "core-js/modules/web.url-search-params.size.js";
import { mapGetters } from 'vuex';
import JSZip from 'jszip';
export default {
  data() {
    return {
      Number: 0,
      showLogoutButton: false,
      tableData: [],
      search: '',
      currentPage: 1,
      pageSize: 15,
      selectedFiles: [],
      sortOption: 'dateDesc',
      isUploading: false,
      showdetailDialog: false,
      detailFile: null,
      activeUrlTab: 'originUrl'
    };
  },
  computed: {
    ...mapGetters(['credentials']),
    filteredTableData() {
      return this.tableData.filter(data => !this.search || data.name.toLowerCase().includes(this.search.toLowerCase()) || data.metadata?.FileName?.toLowerCase().includes(this.search.toLowerCase()));
    },
    paginatedTableData() {
      const sortedData = this.sortData(this.filteredTableData);
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      let data = sortedData.slice(start, end);
      // previewSrcList
      const fullList = data.filter(file => !file.metadata?.FileType?.includes('video')).map(file => `/file/${file.name}`);
      data.forEach(file => {
        if (!file.metadata?.FileType?.includes('video')) {
          // 
          file.previewSrcList = fullList.slice(fullList.indexOf(`/file/${file.name}`)).concat(fullList.slice(0, fullList.indexOf(`/file/${file.name}`)));
        }
      });
      return data;
    },
    sortIcon() {
      return this.sortOption === 'dateDesc' ? 'sort-amount-down' : 'sort-alpha-up';
    },
    dialogWidth() {
      return window.innerWidth > 768 ? '60%' : '90%';
    },
    accessType() {
      if (this.detailFile?.metadata?.ListType === 'White') {
        return '';
      } else if (this.detailFile?.metadata?.ListType === 'Block' || this.detailFile?.metadata?.Label === 'adult') {
        return '';
      } else {
        return '';
      }
    },
    allUrl() {
      return {
        'originUrl': `${document.location.origin}/file/${this.detailFile?.name}`,
        'mdUrl': `![${this.detailFile?.metadata?.FileName || this.detailFile?.name}](${document.location.origin}/file/${this.detailFile?.name})`,
        'htmlUrl': `<img src="${document.location.origin}/file/${this.detailFile?.name}" alt="${this.detailFile?.metadata?.FileName || this.detailFile?.name}" width=100%>`,
        'bbUrl': `[img]${document.location.origin}/file/${this.detailFile?.name}[/img]`,
        'tgId': this.detailFile?.metadata?.TgFileId || ''
      };
    },
    tableColumnNum() {
      return window.innerWidth > 768 ? 3 : 1;
    },
    tablePreviewSpan() {
      return window.innerWidth > 768 ? 2 : 1;
    },
    disableTooltip() {
      return window.innerWidth < 768;
    },
    selectPage() {
      //  true false
      return this.paginatedTableData.every(file => file.selected);
    },
    selectPageIcon() {
      return this.selectPage ? 'check-square' : 'square';
    }
  },
  watch: {
    tableData: {
      handler(newData) {
        this.selectedFiles = newData.filter(file => file.selected);
      },
      deep: true
    },
    sortOption(newOption) {
      localStorage.setItem('sortOption', newOption);
    }
  },
  methods: {
    refreshDashboard() {
      location.reload();
    },
    handleDownload(key) {
      const link = document.createElement('a');
      link.href = `/file/${key}`;
      link.download = key;
      link.click();
    },
    openDetailDialog(index, key) {
      this.detailFile = this.paginatedTableData[index];
      this.showdetailDialog = true;
    },
    handleTabClick(tab) {
      this.activeUrlTab = tab.props.name;
    },
    handleUrlClick(event) {
      // 
      navigator.clipboard.writeText(event.target.value).then(() => {
        this.$message({
          type: 'success',
          message: ''
        });
      }).catch(() => {
        this.$message({
          type: 'error',
          message: ''
        });
      });
    },
    handleDetailDelete(key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.fetchWithAuth(`/api/manage/delete/${key}`, {
          method: 'GET'
        }).then(response => {
          if (response.ok) {
            const fileIndex = this.tableData.findIndex(file => file.name === key);
            if (fileIndex !== -1) {
              this.tableData.splice(fileIndex, 1);
            }
          } else {
            return Promise.reject('');
          }
        }).then(() => {
          this.updateStats();
          this.$message.success('!');
          this.showdetailDialog = false;
        }).catch(() => this.$message.error(''));
      }).catch(() => this.$message.info(''));
    },
    handleBlock(key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.fetchWithAuth(`/api/manage/block/${key}`, {
          method: 'GET'
        }).then(response => {
          if (response.ok) {
            const fileIndex = this.tableData.findIndex(file => file.name === key);
            if (fileIndex !== -1) {
              this.tableData[fileIndex].metadata.ListType = 'Block';
            }
          } else {
            return Promise.reject('');
          }
        }).then(() => {
          this.$message.success('!');
        }).catch(() => this.$message.error(''));
      }).catch(() => console.log(''));
    },
    handleWhite(key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.fetchWithAuth(`/api/manage/white/${key}`, {
          method: 'GET'
        }).then(response => {
          if (response.ok) {
            const fileIndex = this.tableData.findIndex(file => file.name === key);
            if (fileIndex !== -1) {
              this.tableData[fileIndex].metadata.ListType = 'White';
            }
          } else {
            return Promise.reject('');
          }
        }).then(() => {
          this.$message.success('!');
        }).catch(() => this.$message.error(''));
      }).catch(() => console.log(''));
    },
    async fetchWithAuth(url, options = {}) {
      // , url  /api
      // url = `/api${url}`;
      if (this.credentials) {
        //  Authorization 
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        //  cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    handleDelete(index, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.fetchWithAuth(`/api/manage/delete/${key}`, {
          method: 'GET'
        }).then(response => {
          if (response.ok) {
            const fileIndex = this.tableData.findIndex(file => file.name === key);
            if (fileIndex !== -1) {
              this.tableData.splice(fileIndex, 1);
            }
          } else {
            return Promise.reject('');
          }
        }).then(() => {
          this.updateStats();
          this.$message.success('!');
        }).catch(() => this.$message.error(''));
      }).catch(() => this.$message.info(''));
    },
    handleBatchDelete() {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const promises = this.selectedFiles.map(file => this.fetchWithAuth(`/api/manage/delete/${file.name}`, {
          method: 'GET'
        }));
        Promise.all(promises).then(results => {
          results.forEach((response, index) => {
            if (response.ok) {
              const fileIndex = this.tableData.findIndex(file => file.name === this.selectedFiles[index].name);
              if (fileIndex !== -1) {
                this.tableData.splice(fileIndex, 1);
              }
            }
          });
          this.selectedFiles = [];
          this.updateStats();
          this.$message.success('!');
        }).catch(() => this.$message.error(''));
      }).catch(() => this.$message.info(''));
    },
    handleBatchCopy() {
      const links = this.selectedFiles.map(file => `${document.location.origin}/file/${file.name}`).join('\n');
      navigator.clipboard ? navigator.clipboard.writeText(links).then(() => this.$message.success('~')) : this.copyToClipboardFallback(links);
    },
    copyToClipboardFallback(text) {
      const textarea = document.createElement('textarea');
      document.body.appendChild(textarea);
      textarea.style.position = 'fixed';
      textarea.style.clip = 'rect(0 0 0 0)';
      textarea.style.top = '10px';
      textarea.value = text;
      textarea.select();
      document.execCommand('copy');
      document.body.removeChild(textarea);
      this.$message.success('~');
    },
    handleGoUpload() {
      this.$router.push('/');
    },
    handleGoToAdmin() {
      this.$router.push('/customerConfig');
    },
    handleCopy(index, key) {
      const text = `${document.location.origin}/file/${key}`;
      navigator.clipboard ? navigator.clipboard.writeText(text).then(() => this.$message.success('~')) : this.copyToClipboardFallback(text);
    },
    handlePageChange(page) {
      this.currentPage = page;
    },
    updateStats() {
      this.Number = this.tableData.length;
    },
    sort(command) {
      this.sortOption = command;
    },
    sortData(data) {
      return this.sortOption === 'nameAsc' ? data.sort((a, b) => a.name.localeCompare(b.name)) : data.sort((a, b) => b.metadata.TimeStamp - a.metadata.TimeStamp);
    },
    handleVideoClick(event) {
      const videoElement = event.target;
      if (videoElement.requestFullscreen) {
        videoElement.requestFullscreen();
      } else if (videoElement.webkitRequestFullscreen) {
        // Safari/Old Chrome
        videoElement.webkitRequestFullscreen();
      } else if (videoElement.mozRequestFullScreen) {
        // Firefox
        videoElement.mozRequestFullScreen();
      } else if (videoElement.msRequestFullscreen) {
        // IE/Edge
        videoElement.msRequestFullscreen();
      }
    },
    handleLogout() {
      this.$store.commit('setCredentials', null);
      this.$router.push('/adminLogin');
    },
    handleSelectPage() {
      if (this.selectPage) {
        this.paginatedTableData.forEach(file => file.selected = false);
      } else {
        this.paginatedTableData.forEach(file => file.selected = true);
      }
    },
    handleBatchAction(command) {
      if (command === 'copy') {
        this.handleBatchCopy();
      } else if (command === 'delete') {
        this.handleBatchDelete();
      } else if (command === 'download') {
        this.handleBatchDownload();
      }
    },
    handleBatchDownload() {
      //  zip 
      const zip = new JSZip();
      const folder = zip.folder('files');
      // Promise
      const fileNameCount = {}; // 

      const downloadPromises = this.selectedFiles.map(file => {
        return fetch(`/file/${file.name}`).then(response => response.blob()).then(blob => {
          // 
          let fileName = file.metadata?.FileName || file.name;
          if (fileNameCount[fileName]) {
            // 
            const extension = fileName.substring(fileName.lastIndexOf('.'));
            const baseName = fileName.substring(0, fileName.lastIndexOf('.'));
            fileName = `${baseName}(${fileNameCount[fileName]})${extension}`;
            fileNameCount[file.name]++;
          } else {
            // 1
            fileNameCount[fileName] = 1;
          }

          //  zip 
          folder.file(fileName, blob);
        });
      });
      Promise.all(downloadPromises).then(() => zip.generateAsync({
        type: 'blob'
      })).then(blob => {
        const link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = 'files.zip';
        link.click();
      });
    }
  },
  mounted() {
    this.fetchWithAuth("/api/manage/check", {
      method: 'GET'
    }).then(response => response.text()).then(result => {
      if (result == "true") {
        this.showLogoutButton = true;
        //  check  list  fetch 
        return this.fetchWithAuth("/api/manage/list", {
          method: 'GET'
        });
      } else if (result == "Not using basic auth.") {
        return this.fetchWithAuth("/api/manage/list", {
          method: 'GET'
        });
      } else {
        throw new Error('Unauthorized');
      }
    }).then(response => response.json()).then(result => {
      this.tableData = result.map(file => ({
        ...file,
        selected: false
      }));
      this.updateStats();
      const savedSortOption = localStorage.getItem('sortOption');
      if (savedSortOption) {
        this.sortOption = savedSortOption;
      }
      this.sortData(this.tableData);
    }).catch(err => {
      if (err.message !== 'Unauthorized') {
        this.$message.error('');
      }
    });
  }
};